@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@400;500;600&family=Poppins:wght@200;300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body, #root{
  width: 100%;
  height: auto;
  margin: 0;
  padding: 0;

}

body{
  background-color: #010512;
}


.navbar_outer{
    width: 85%;
    margin: auto;

}

.main_avatar{
    cursor: pointer;
    width: 25px !important;
    height: auto !important;
}

.logo_title{
    color: #fff;
    font-family: 'Oswald', sans-serif;
    font-weight: 300;
}

.logo_container{
    display: flex;
    align-items: center;
}

.logo_container img{
    width: auto;
    height: 40px;
   
}



.navigation_list{
display: flex;
justify-content: space-between;
align-items: center;
}

#avatar_pc{
    margin-left: 20px;
    display: none;
}

.right_nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
}


#avatar{
    display: none;
    margin-right: 20px;
}

ul{
    list-style-type: none;
    font-family: 'Poppins', sans-serif;
    color: #fff;
    display: flex;
    float: right;
}

.li{
    margin:0 20px 0 20px;
}

.navbar_outer{
    display: flex;
    justify-content: space-between;
    align-items: center;
}


*{
    text-decoration: none;
}

a{
    color:#fff
}
.main_heading{
    font-size: 50px;
    font-family: 'Oswald', sans-serif;
    margin-top: 15vh;
    color:#fff;
    font-weight: 400;
    z-index: 1;
    
}

.ham{
    display: none;
}
.navigations{
    align-items: center;
    align-content: center;
}

.ham{
    color: #fff;
    display: none;
}

.landing_inner_col1, .landing_inner_col2{
    width: 50%;
    flex:0.5 1;
    
}

.cross{
    display: none;
    color: #fff;
}


.landing_outer{
    width: 85%;
    margin: auto;
}

.landing_inner{
    display: flex;
    justify-content: space-between;
}

.man{
    justify-content: center;
    width: 150%;
    z-index: -1;
}

.landing_inner_col2{
    display: flex;
    justify-content: center;
}

.main_button{
    background-color:#575BC7;
    border: none;
    color: #fff;
    padding: 2% 15%;
    font-size: 22px;
    font-family: 'Oswald', sans-serif;
}


@media(max-width:1024px){
    .main_heading{
        font-size: 40px;
      
    }
}


@media(min-width:768px){
    
    #avatar{
        display: none !important;
        cursor:pointer;

    }


    .right_nav{
        display: none !important;
    }
}



@media(max-width:768px){

    .landing_inner{
    display: block;
    }

    #avatar_pc{
        display: none !important;
        cursor:pointer;
    }

    .ham{
        display: block;
        z-index: 11;
    }

    .cross{
        z-index: 100;
    }


    .landing_inner_col1, .landing_inner_col2{
        width: 100%;
        flex: 1 1;
    }

    .main_heading{
        justify-content: center;
        text-align: center;
    }


     .flex_button{
         display: flex;
         width: 100%;
         justify-content: center;

     }


     .navigation_list{
         z-index: 1;

     }


     #ul{
         display: block;
         width: 50%;
         position: absolute;
         background-color: #575BC7;
         right:-100%;
         top:0;
         margin:0;
         padding: 0 !important;
         text-align: center;
         padding-top: 20px !important;
         transition: 0.4s ease-in-out;

     }


    
    .man{
      margin-top: -110px;
      width: 5000px;
       }

       #ul{
           padding-right: 10%;
           height: 100vh;
       }

       .li{
        margin:80px 20px 80px 20px
    }
}

@media(max-width:500px){
    .main_heading{
        font-size: 30px;
        margin-top:12vh;
    }
        
    .man{
      margin-top: -80px;
      width: 5000px;
     }

   

}



#loader p{
    color:#fff !important;
    text-align: center;
    margin: 20% 0;
}


.posts{
    display: flex;
    flex-wrap: wrap;
}

.card_header{
    width: 47.5%;
    border:1px solid rgba(255,255,255,0.2);
    padding:2% 0;
    margin:1%
}

.image_post{
    display: flex;
    width: 80% !important;
    margin:auto;
    padding-top: 20px;

}

#image_list{
    display: block !important;
    padding:0;
}

.tips_section{
    display: flex;
    justify-content: space-around;
    padding-top: 20px;
}

.author{
    color:#fff
}

.head{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    border-bottom: 1px solid rgba(255,255,255,0.2);
    padding-bottom: 10px;
}


.whole_body{
    width: 85%;
    margin:auto;
}


#image_list li{
    margin:0;
}

.image_desc{
    padding: 0 25px;
}


@media(max-width:860px){
.posts{
}

.card_header{
    width: 100%;
    height: 100%;
}
.image_post{
    display: flex;
    width: 80% !important;
    margin:auto;
    padding-top: 20px;

}
}


body{
    width: 100%;
    overflow-x: hidden;
}
.input-form{
    color: #fff;

}

h2{
    color:#fff
}

.form_inner{
    text-align: center;
    padding:30px 0;
    margin:20px 0;
    border:1px solid white;
}

.submit_button{
    margin-top:20px;
    width: 30%;
}

#imageDescription{
    width: 30%;
    height: 40px;
}
